.cards {
  padding: 4rem;
  background: white;
}

.projects-title {
  font-size: 40px;
  text-align: center;
  text-decoration: underline overline;
  text-decoration-color: tomato;
  text-decoration-thickness: 6px;
}

.cards-container {
  display: flex;
  flex-flow: column;
  align-items: center;
  max-width: 1120px;
  width: 90%;
  margin: 0 auto;
}

.cards-wrapper {
  position: relative;
  margin: 50px 0 45px;
}

.cards-items {
  margin-bottom: 24px;
}

.card-item {
  display: flex;
  flex: 1;
  margin: 0 1rem;
  border-radius: 10px;
  background-color: tomato;
  color: white;
}

.card-item-link {
  display: flex;
  flex-flow: column;
  width: 100%;
  box-shadow: 0 6px 20px rgba(56, 125, 255, 0.17);
  -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
  filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
  border-radius: 10px;
  overflow: hidden;
  text-decoration: none;
}

.card-item-picture-wrap {
  position: relative;
  width: 100%;
  padding-top: 67%;
  overflow: hidden;
}

.fade-img {
  animation-name: fade-img;
  animation-duration: 2s;
}

.card-item-picture-wrap::after {
  content: attr(data-category);
  position: absolute;
  bottom: 0;
  padding: 6px 8px;
  max-width: 40%;
  font-size: 15px;
  font-weight: 900;
  color: black;
  background-color: #fdfd96;
  box-sizing: border-box;
  border-radius: 3%;
}

.card-item-img {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  max-width: 100%;
  height: 100%;
  max-height: 100%;
  object-fit: cover;
  transition: all 0.2s linear;
}

.card-item-img:hover {
  transform: scale(1.1);
}

.card-item-info {
  padding: 20px 20px 20px 20px;
}

.card-item-tech {
  padding: 20px 20px 30px 20px;
}

.card-item-text {
  color: white;
  font-size: 18px;
  line-height: 24px;
}

@media only screen and (min-width: 1200px) {
  .content--blog--container {
    width: 84%;
  }
}

@media only screen and (min-width: 1024px) {
  .cards-items {
    display: flex;
  }
}

@media only screen and (max-width: 1024px) {
  .card-item {
    margin-bottom: 2rem;
  }
}

@media screen and (max-width: 768px) {
  .card-item-picture-wrap::after {
    content: attr(data-category);
    position: absolute;
    bottom: 0;
    padding: 6px 8px;
    max-width: 70%;
    font-size: 15px;
    font-weight: 900;
    color: black;
    background-color: #fdfd96;
    box-sizing: border-box;
    border-radius: 3%;
  }

  .card-item-text {
    color: white;
    font-size: 15px;
    line-height: 24px;
  }
}
