.education {
  padding: 4rem;
  background: white;
}

.education-title {
  font-size: 40px;
  display: flex;
  flex-flow: column;
  justify-content: space-between;
  align-items: center;
  padding: 0px 10px 0px 10px;
  text-decoration: underline overline;
  text-decoration-color: tomato;
  text-decoration-thickness: 6px;
}

.education-info {
  display: flex;
  flex-flow: column;
  align-items: center;
  margin-top: 20px;
  transition: transform 0.2s; /* Animation */
}

.education-info:hover {
  transform: scale(1.1); /* zoom the text */
}

.education-item-heading {
  color: rgba(255, 99, 71, 0.904);
  margin-top: 20px;
  margin-bottom: 5px;
}

.education-item-institution {
  color: rgba(0, 0, 0, 0.7);
}

.education-item-location {
  color: rgba(0, 0, 0, 0.7);
}

/* Next section deals with different screen sizes */
@media screen and (max-width: 960px) {
  .education-item-heading {
    font-size: 35px;
    text-align: center;
  }

  .education-item-institution {
    font-size: 15px;
    text-align: center;
    padding: 10px 10px 10px 10px;
  }

  .education-item-location {
    font-size: 15px;
    text-align: center;
  }
}

@media screen and (max-width: 768px) {
  .education-item-heading {
    font-size: 25px;
    text-align: center;
  }

  .education-item-institution {
    font-size: 15px;
    text-align: center;
    padding: 10px 10px 10px 10px;
  }

  .education-item-location {
    font-size: 15px;
    text-align: center;
  }
}
