.project-wrapper {
  display: block;
  padding: 40px 40px 40px 40px;
}

.budgetBuddy-container {
  padding: 30px;
  background: tomato;
  height: 100%; /* cover the size of the screen */
  width: 100%;
  display: flex;
  flex-flow: column;
  align-items: center;
}

.column {
  float: left;
  width: 33.33%;
  padding: 5px;
}

.row::after {
  content: "";
  clear: both;
  display: table;
  padding: 10px 10px 10px 10px;
}

.techUsed-title {
  padding-top: 30px;
  color: rgba(48, 47, 47, 0.699);
}

.tech-column {
  float: left;
  padding: 20px 20px 30px 20px;
}

.tech-row::after {
  content: "";
  clear: both;
  display: table;
  padding: 10px 10px 0px 10px;
}

.tech-text {
  color: rgba(48, 47, 47, 0.562);
  border: 4px solid rgb(241, 79, 50);
  border-radius: 5%;
  padding: 10px 10px 10px 10px;
}

.project-picture {
  width: 100%;
  padding: 0px 10px 10px 10px;
}

.project-description {
  text-align: center;
  padding: 5px 500px 0px 500px;
  font-size: 22px;
  font-weight: 400;
  color: white;
}

.back-home-button:link,
.back-home-button:visited {
  color: rgba(48, 47, 47, 0.562);
  font-weight: bolder;
  text-decoration: none;
  display: inline-block;
  border: 4px solid rgb(241, 79, 50);
  background: tomato;
}

.back-home-button:hover {
  border: 4px solid #fdfd96;
  color: white;
}

.budgetBuddy-title {
  font-size: 80px;
  padding-top: 0px;
  color: white;
}

.budgetBuddy-motto {
  font-size: 20px;
  padding-left: 200px;
  padding-top: 7px;
  color: rgba(48, 47, 47, 0.562);
  padding-bottom: 60px;
}

.back-home-button {
  padding: 15px 20px;
  margin: 10px 30px 30px 30px;
  border-radius: 50%;
  font-size: 23px;
  transition: 0.3s;
}

@media only screen and (max-width: 1600px) {
  .budgetBuddy-title {
    font-size: 50px;
  }

  .budgetBuddy-motto {
    font-size: 15px;
    color: rgba(48, 47, 47, 0.562);
    padding-bottom: 30px;
  }

  .project-description {
    text-align: center;
    padding: 5px 200px 0px 200px;
    font-size: 17px;
    font-weight: 400;
  }

  .tech-column {
    float: left;
    padding: 10px 12px 10px 12px;
  }

  .tech-row::after {
    content: "";
    clear: both;
    display: table;
    padding: 5px 5px 0px 5px;
  }

  .tech-text {
    font-size: 15px;
  }

  .techUsed-title {
    padding-top: 20px;
    color: rgba(48, 47, 47, 0.699);
    font-size: 20px;
  }
}

@media screen and (max-width: 768px) {
  .budgetBuddy-title {
    font-size: 45px;
    padding-top: 0px;
  }
  .budgetBuddy-motto {
    font-size: 15px;
    padding-left: 5px;
    padding-top: 7px;
    color: rgba(48, 47, 47, 0.562);
    padding-bottom: 40px;
  }

  .column {
    float: left;
    width: 100%;
    padding: 5px;
  }

  .row::after {
    content: "";
    clear: both;
    display: table;
    padding: 0px 0px 0px 0px;
  }

  .project-description {
    text-align: center;
    padding: 20px 0px 20px 0px;
    font-size: 15px;
  }

  .techUsed-title {
    padding-top: 10px;
    font-size: 18px;
  }

  .tech-column {
    float: left;
    width: 50%;
    padding: 10px 5px 10px 0px;
    text-align: center;
  }

  .tech-row::after {
    content: "";
    clear: both;
    display: table;
    padding: 5px 10px 0px 10px;
  }

  .tech-text {
    font-size: 9px;
  }
}
