video {
  object-fit: cover;
  width: 100%;
  height: 140%;
  position: fixed; /* stays in one place*/
  z-index: -1; /* to prevent it from covering other objects*/

  /* Make the background video appear blurry */
  -o-filter: blur(2px);
  -webkit-filter: blur(2px);
          filter: blur(2px);
  object-fit: cover;
  transform: scale(1.04);
}

.main-container {
  background: linear-gradient(90deg, tomato 50%, white 50%);
  height: 90vh; /* cover the size of the screen */
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  object-fit: contain;
}

