/*Social Media*/
.social-container {
  background: linear-gradient(90deg, white 50%, tomato 50%);
  padding: 1rem 1rem 1rem 1rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.social-icon-link {
  font-size: 24px;
  transition: transform 0.2s;
}

.social-icon-link:hover {
  transform: scale(1.1);
  color: white;
  transition: all 0.3s ease-out;
}

.social-media-wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 1000px;
}

.social-icons {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 105px;
  padding-top: 5px;
  padding-right: 10px;
  padding-left: 10px;
  border-radius: 3%;
}

.social-media-img {
  height: 24px;
  width: 24px;
}

.main-btn {
  margin-top: 32px;
}

.main-btn .btn {
  margin: 6px;
}

.large-btn {
  background-color: transparent;
  color: rgb(255, 255, 255);
  padding: 15px 25px;
  border: 3px solid #ff6961;
  transition: all 0.3s ease-out;
}

.large-btn:hover {
  background: #ff6961;
  color: white;
  transition: all 0.3s ease-out;
}

