.navbar {
  background: linear-gradient(90deg, tomato 50%, white 50%);
  transition: 200ms ease-in-out;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: -webkit-sticky;
  position: sticky; /* stay visible when scrolling */
  top: 0;
  z-index: 1000; /* on top of everything*/
  font-weight: 900;
}

/* change navbar to color */
.navbar.active {
  background: tomato;
}

.navbar-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80px;
  max-width: 1500px;
}

.nav-menu {
  display: grid;
  grid-template-columns: repeat(5, auto);
  grid-gap: 10px; /* space out nav links*/
  list-style: none;
  text-align: center;
  margin-right: 2rem;
}

.nav-links {
  color: black;
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 0.5rem 1rem;
  height: 100%;
}

.nav-links:hover {
  border-bottom: 4px solid #fdfd96;
  border-top: 4px solid #fdfd96;
  transition: all 0.2s ease-out;
}

/* Harmburger menu icon */
.fa-bars {
  color: black;
}

.menu-icon {
  display: none;
}

/* Handle smaller screen */
@media screen and (max-width: 960px) {
  .NavbarItems {
    position: relative;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 90vh;
    position: absolute;
    top: 80px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .nav-menu.active {
    background: tomato;
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
  }

  .nav-links {
    text-align: center;
    padding: 2rem;
    width: 100%;
    display: table;
  }

  .nav-links:hover {
    background-color: #ff6961;
    color: white;
  }

  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
  }

  .fa-times {
    color: #ff6961;
    font-size: 2rem;
  }
}

